.btn-1 {
  font-weight: 100;
  color: white;
  margin: 0px 20px;
}
.btn-1 svg {
  margin: 8px -8px;
  height: 35px;
  position: absolute;
  top: 0;
  border-radius: px;
  color: red;
  width: 70px;
}

.btn-1 rect {
  fill: none;
  stroke: red;
  stroke-width: 2;
  stroke-dasharray: 422, 0;
  transition: all 0.35s linear;
}
.btn-1:hover {
  background: rgba(225, 51, 45, 0);
  font-weight: 900;
  letter-spacing: 1px;
}
.btn-1:hover rect {
  stroke-width: 5;
  stroke-dasharray: 15, 310;
  stroke-dashoffset: 48;
  transition: all 1.35s cubic-bezier(0.19, 1, 0.22, 1);
}
