.gridoverlay,
.header {
  background: linear-gradient(-45deg, #dca510, #e73c7e, #605b61, #1e2020);
  /* linear-gradient(-45deg, #ee7752, #e73c7e, #10495f, #1e2020); */
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
