.header-overlay {
  background: rgba(227, 186, 160, 0.6);
  display: grid;
  position: absolute;
  color: black;
  padding: 30px;
  z-index: 4;
  top: 10rem;
  border-radius: 15px;
  left: 25rem;
}

.button-overlay {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 20px;
  justify-content: center;
}

.text-spell {
  color: crimson;
}

@media only screen and (max-width: 768px) and (min-width: 300px) {
  .header-overlay {
    left: 4% !important;
    padding: 5px !important;
    top: 120px;
  }

  .text-overlay{
    font-size:10px;
  }
}
